<template>
  <div>
    <HelloWorld />
    <div class="bg-o-black full" v-if="loadBg == true" @click="loadBg = false" @load="bg_o_black">
      <div class="chocoW">
        <img src="../assets/drive/choco_w02.png" alt="1">
      </div>
      <br>
      <button class="bgBtn"> WELLCOME</button>
    </div>
    <div class="cloud" v-for="(cloudu, i) in cloud" :key="cloudu" :class="cloud[i]">
      <img src="../assets/drive/cloud.png" alt="cloud">
    </div>
    <div class="pop" v-for="(popo, i) in 5" :key="popo" :class="pop[i]">
      <img src="../assets/drive/pop-1.png" alt="1" class="popping">
    </div>
    <div class="heat" v-for="(heata, i) in 2" :key="heata" :class="ball[i]" @click="modalList = true, modal00()" @mouseenter="hoverin('a')" @mouseleave="hoverout('a')">
      <img :src="require(`../assets/drive/${heat}.png`)" alt="1" class="heatB" @load="balloon" />
    </div>
    <div class="chocoW chocoH" @click="modalList = true, modal02()" @mouseenter="hoverin('c')" @mouseleave="hoverout('c')">
      <img src="../assets/drive/choco_w02.png" alt="1">
    </div>
    <div class="tan tan01" @click="modalList = true, modal01()" @mouseenter="hoverin('b')" @mouseleave="hoverout('b')">
      <img :src="require(`../assets/drive/${tan}.png`)" alt="1" @load="tantan(c)" />
    </div>
    <div class="shop">
      <img :src="require(`../assets/drive/${shopcar}.png`)" alt="1" class="shopping" @load="shoppingCart" />
    </div>
    <div class="hoverchoco" v-for="(chocohover, i) in 3" :key="chocohover" :class="hoverco[i]">
<!--       <div class="clickBtn">CLICK</div> -->
      <img :src="require(`../assets/drive/${hover01[i]}.png`)" alt="hover">
    </div>
    <div class="modal bg-o-black"  v-if="modalList == true" @click="modalList = false">
      <div class="modal_info bg-white" v-for="(list, i) in modal" :key="list" :class="{active:active} && list">
        {{ modalTxt[i] }}
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'HomeView',
  components: {
    HelloWorld
  },
  data () {
    return {
      active: false,
      loadBg: true,
      pop: ['pop01', 'pop02', 'pop03', 'pop04', 'pop05'],
      ball: ['balloon01', 'balloon02'],
      cloud: ['cloud01', 'cloud02'],
      heat: 'heat01',
      shopcar: 'choco_shop01',
      modalList: false,
      modal: ['modal01', 'modal02', 'modal03'],
      modal0: ['modal00', 'modal01'],
      modalTxt: ['Hello Choco-Pie', 'HelloWorld', 'HelloVUE'],
      tan: 'tan01',
      hover01: ['hover01', 'hover01', 'hover01'],
      hoverco: ['hoverchoco01', 'hoverchoco02', 'hoverchoco03'],
      a: 'a',
      b: 'b',
      c: 'c'
    }
  },
  methods: {
    bg_o_black () {
      setTimeout(() => {
        this.loadBg = false
      }, 1000)
    },
    balloon () {
      if (this.heat === 'heat01') {
        setTimeout(() => {
          this.heat = 'heat02'
        }, 500)
      } else if (this.heat === 'heat02') {
        setTimeout(() => {
          this.heat = 'heat01'
        }, 500)
      }
    },
    shoppingCart () {
      if (this.shopcar === 'choco_shop01') {
        setTimeout(() => {
          this.shopcar = 'choco_shop02'
        }, 1000)
      } else if (this.shopcar === 'choco_shop02') {
        setTimeout(() => {
          this.shopcar = 'choco_shop01'
        }, 1000)
      }
    },
    tantan () {
      if (this.tan === 'tan01') {
        setTimeout(() => {
          this.tan = 'tan02'
        }, 500)
      } else if (this.tan === 'tan02') {
        setTimeout(() => {
          this.tan = 'tan01'
        }, 500)
      }
    },
    modal00 () {
      const heat = document.querySelector('.balloon01')
      if (heat.className === 'heat balloon01') {
        console.log('001')
        this.modal[0] = 'modal01 active'
        this.modal[1] = 'modal02'
        this.modal[2] = 'modal03'
      }
    },
    modal01 () {
      this.modal[0] = 'modal01'
      this.modal[1] = 'modal02 active'
      this.modal[2] = 'modal03'
    },
    modal02 () {
      this.modal[0] = 'modal01'
      this.modal[1] = 'modal02'
      this.modal[2] = 'modal03 active'
    },
    hoverin (e) {
      if (e === 'a') {
        this.hover01[0] = 'hover02'
      } else if (e === 'b') {
        this.hover01[1] = 'hover02'
      } else if (e === 'c') {
        this.hover01[2] = 'hover02'
      }
    },
    hoverout (e) {
      if (e === 'a') {
        this.hover01[0] = 'hover01'
      } else if (e === 'b') {
        this.hover01[1] = 'hover01'
      } else if (e === 'c') {
        this.hover01[2] = 'hover01'
      }
    }
  }
}
</script>
<style scoped>
  @import url(../../css/common.css);
  @import url(../../css/home.css);
  .full {
    width: 100%;
    height: 100vh;
    position: fixed; top:0; left: 0;
    z-index: 99;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    }
  .bgBtn {
    width: 30%;
    border: none;
    color: #fff;
    font-size: 20px;
    cursor: pointer;
  }
</style>
